html,
body,
#root,
.App {
  height: 100%;
}

.App {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}

body {
  background: #000;
  margin: 0;
  font-family: monospace;
  font-size: 4em;
  color: #fff;
}

input[type="range"] {
  width: 100%;
  margin: 32px 0;
  background-color: transparent;
  -webkit-appearance: none;
}
input[type="range"]:focus {
  outline: none;
}
input[type="range"]::-webkit-slider-runnable-track {
  background: #222;
  width: 100%;
  height: 36px;
  cursor: pointer;
}
input[type="range"]::-webkit-slider-thumb {
  width: 36px;
  height: 36px;
  background: #ffffff;
  border: 0;
  cursor: pointer;
  -webkit-appearance: none;
}
input[type="range"]:focus::-webkit-slider-runnable-track {
  background: #222;
}
input[type="range"]::-moz-range-track {
  background: #222;
  border: 0.2px solid #010101;
  border-radius: 1.3px;
  width: 100%;
  height: 36px;
  cursor: pointer;
}
input[type="range"]::-moz-range-thumb {
  width: 3px;
  height: 36px;
  background: #ffffff;
  border: 0;
  cursor: pointer;
}
input[type="range"]::-ms-track {
  background: transparent;
  border-color: transparent;
  border-width: 1px 0;
  color: transparent;
  width: 100%;
  height: 36px;
  cursor: pointer;
}
input[type="range"]::-ms-fill-lower {
  background: #222;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
}
input[type="range"]::-ms-fill-upper {
  background: #222;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
}
input[type="range"]::-ms-thumb {
  width: 3px;
  height: 36px;
  background: #ffffff;
  border: 0;
  cursor: pointer;
  margin-top: 0px;
  /*Needed to keep the Edge thumb centred*/
}
input[type="range"]:focus::-ms-fill-lower {
  background: #222;
}
input[type="range"]:focus::-ms-fill-upper {
  background: #222;
}
